/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { useState, useEffect } from 'react';
import { useSnackbar } from 'notistack';
import {
    Card,
    Table,
    Button,
    TableRow,
    TableBody,
    TableCell,
    Container,
    Typography,
    TableContainer,
    TablePagination,
    Box,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    TextField,
    IconButton,
    Tooltip,
    Tabs,
    Tab,
    LinearProgress,
    MenuItem,
    Autocomplete,
    Avatar,
    Checkbox,
    Stack,
} from '@mui/material';
import swal from 'sweetalert';
import useSettings from '../../../hooks/useSettings';
import Page from '../../../components/Page';
import Label from '../../../components/Label';
import Iconify from '../../../components/Iconify';
import Scrollbar from '../../../components/Scrollbar';
import SearchNotFound from '../../../components/SearchNotFound';
import HeaderBreadcrumbs from '../../../components/HeaderBreadcrumbs';
import { fCurrency } from '../../../utils/formatNumber';
import axios from '../../../utils/axios';
import useResponsive from '../../../hooks/useResponsive';
import { UserListHead, UserListToolbar } from '../../../sections/admin/user/list';
import { NIGERIAN_LOCATIONS, STATES } from '../../../utils/locations';

// ----------------------------------------------------------------------

const CHARITY_HEAD = [
    { id: 'name', label: 'Organization Name', alignRight: false },
    { id: 'username', label: 'Handle', alignRight: false },
    { id: 'category', label: 'Category', alignRight: false },
    { id: 'cac_number', label: 'CAC Number', alignRight: false },
    { id: 'pending_balance', label: 'Escrow (Pending)', alignRight: false },
    { id: 'available_balance', label: 'Released', alignRight: false },
    { id: 'verification_status', label: 'Status', alignRight: false },
    { id: 'action', label: 'Action', alignRight: true },
];

const CAMPAIGN_HEAD = [
    { id: 'title', label: 'Campaign Title', alignRight: false },
    { id: 'charity_name', label: 'Organization', alignRight: false },
    { id: 'target_amount', label: 'Target', alignRight: false },
    { id: 'progress', label: 'Progress', alignRight: false },
    { id: 'status', label: 'Status', alignRight: false },
    { id: 'payout_status', label: 'Payout', alignRight: false },
    { id: 'action', label: 'Action', alignRight: true },
];

const CATEGORIES = [
    'Orphanage', 'Mosque', 'Church', 'Medical', 'Prison', 'Education', 'Emergency'
];

// ----------------------------------------------------------------------

export default function CharityManagement() {
    const { themeStretch } = useSettings();
    const { enqueueSnackbar } = useSnackbar();
    const isDesktop = useResponsive('up', 'sm');
    const isMobile = useResponsive('down', 'sm');
    const AccessToken = window.localStorage.getItem('accessToken');

    const [currentTab, setCurrentTab] = useState('organizations');
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [total, setTotal] = useState(0);
    const [loading, setLoading] = useState(true);
    const [filterName, setFilterName] = useState('');

    const [charityList, setCharityList] = useState([]);
    const [campaignList, setCampaignList] = useState([]);

    // For Username Auto-complete
    const [userOptions, setUserOptions] = useState([]);
    const [searchLoading, setSearchLoading] = useState(false);

    // Modals
    const [openAddCharity, setOpenAddCharity] = useState(false);
    const [openAddCampaign, setOpenAddCampaign] = useState(false);
    const [isEdit, setIsEdit] = useState(false);
    const [currentCharityId, setCurrentCharityId] = useState(null);

    // Form States
    const [charityForm, setCharityForm] = useState({
        name: '', username: '', category: 'Orphanage', cac_number: '', description: '', logo: '', verification_status: 'verified'
    });
    const [campaignForm, setCampaignForm] = useState({
        charity_id: '', title: '', description: '', target_amount: '',
        status: 'active', image: '', state: 'Kano', lga: 'Kano Municipal'
    });

    useEffect(() => {
        if (currentTab === 'organizations') {
            fetchCharities();
        } else {
            fetchCampaigns();
        }
    }, [currentTab, page, rowsPerPage, filterName]);

    const fetchCharities = async () => {
        setLoading(true);
        try {
            const response = await axios.get(`/api/admin/charities/${AccessToken}/secure?page=${page}&limit=${rowsPerPage}&search=${filterName}`);
            if (response.data.status === 200) {
                setCharityList(response.data.charities.data);
                setTotal(response.data.charities.total);
            }
        } catch (error) {
            enqueueSnackbar('Failed to fetch charities', { variant: 'error' });
        } finally {
            setLoading(false);
        }
    };

    const fetchCampaigns = async () => {
        setLoading(true);
        try {
            const response = await axios.get(`/api/charity/campaigns?page=${page}&limit=${rowsPerPage}&search=${filterName}`);
            if (response.data.status === 200) {
                setCampaignList(response.data.campaigns.data);
                setTotal(response.data.campaigns.total);
            }
        } catch (error) {
            enqueueSnackbar('Failed to fetch campaigns', { variant: 'error' });
        } finally {
            setLoading(false);
        }
    };

    const handleUserSearch = async (val) => {
        if (!val || val.length < 2) return;
        setSearchLoading(true);
        try {
            const response = await axios.get(`/api/admin/users/search/${AccessToken}/secure?search=${val}`);
            if (response.data.status === 200) {
                setUserOptions(response.data.users);
            }
        } catch (error) {
            console.error(error);
        } finally {
            setSearchLoading(false);
        }
    };

    const handleLogoUpload = (e) => {
        const file = e.target.files[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = () => {
                setCharityForm({ ...charityForm, logo: reader.result });
            };
            reader.readAsDataURL(file);
        }
    };

    const handleCampaignImage = (e) => {
        const file = e.target.files[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = () => {
                setCampaignForm({ ...campaignForm, image: reader.result });
            };
            reader.readAsDataURL(file);
        }
    };

    const handleAddCharity = async () => {
        try {
            const endpoint = isEdit ? '/api/admin/charity/update' : '/api/admin/charity/add';
            const payload = {
                ...charityForm,
                id: AccessToken,
                charity_id: currentCharityId
            };
            const response = await axios.post(endpoint, payload);
            if (response.data.status === 200) {
                enqueueSnackbar(response.data.message, { variant: 'success' });
                setOpenAddCharity(false);
                fetchCharities();
            }
        } catch (error) {
            enqueueSnackbar(isEdit ? 'Error updating charity' : 'Error onboarding charity', { variant: 'error' });
        }
    };

    const handleEditCharity = (charity) => {
        setIsEdit(true);
        setCurrentCharityId(charity.id);
        setCharityForm({
            name: charity.name,
            username: charity.username,
            category: charity.category,
            cac_number: charity.cac_number || '',
            description: charity.description || '',
            logo: charity.logo_url || '',
            verification_status: charity.verification_status
        });
        setOpenAddCharity(true);
    };

    const handleOpenAddCharity = () => {
        setIsEdit(false);
        setCurrentCharityId(null);
        setCharityForm({
            name: '', username: '', category: 'Orphanage', cac_number: '', description: '', logo: '', verification_status: 'verified'
        });
        setOpenAddCharity(true);
    };

    const handleAddCampaign = async () => {
        try {
            const response = await axios.post(`/api/admin/campaign/add`, {
                ...campaignForm,
                id: AccessToken
            });
            if (response.data.status === 200) {
                enqueueSnackbar(response.data.message, { variant: 'success' });
                setOpenAddCampaign(false);
                fetchCampaigns();
            }
        } catch (error) {
            enqueueSnackbar('Error creating campaign', { variant: 'error' });
        }
    };

    const handleDeleteCharity = async (id) => {
        const willDelete = await swal({
            title: "Are you sure?",
            text: "Once deleted, you will not be able to recover this charity and its campaigns!",
            icon: "warning",
            buttons: true,
            dangerMode: true,
        });

        if (willDelete) {
            try {
                const response = await axios.post('/api/admin/charity/delete', { id: AccessToken, charity_id: id });
                if (response.data.status === 200) {
                    enqueueSnackbar(response.data.message, { variant: 'success' });
                    fetchCharities();
                } else {
                    enqueueSnackbar(response.data.message, { variant: 'error' });
                }
            } catch (error) {
                enqueueSnackbar(error.response?.data?.message || 'Error deleting charity', { variant: 'error' });
            }
        }
    };

    const handleDeleteCampaign = async (id) => {
        const willDelete = await swal({
            title: "Are you sure?",
            text: "Once deleted, you will not be able to recover this campaign!",
            icon: "warning",
            buttons: true,
            dangerMode: true,
        });

        if (willDelete) {
            try {
                const response = await axios.post('/api/admin/campaign/delete', { id: AccessToken, campaign_id: id });
                if (response.data.status === 200) {
                    enqueueSnackbar(response.data.message, { variant: 'success' });
                    fetchCampaigns();
                } else {
                    enqueueSnackbar(response.data.message, { variant: 'error' });
                }
            } catch (error) {
                enqueueSnackbar(error.response?.data?.message || 'Error deleting campaign', { variant: 'error' });
            }
        }
    };

    const handleProcessPayouts = async () => {
        try {
            const response = await axios.post(`/api/admin/charity/payouts`, { id: AccessToken });
            enqueueSnackbar(response.data.message, { variant: 'info' });
            fetchCampaigns();
            fetchCharities();
        } catch (error) {
            enqueueSnackbar('Payout process failed', { variant: 'error' });
        }
    };

    const handleSinglePayout = async (id) => {
        const confirm = await swal({
            title: "Process Payout?",
            text: "This will release funds and close the campaign.",
            icon: "info",
            buttons: ["Cancel", "Process"],
        });

        if (confirm) {
            try {
                const response = await axios.post(`/api/admin/charity/payouts`, { id: AccessToken, campaign_id: id });
                enqueueSnackbar(response.data.message, { variant: 'success' });
                fetchCampaigns();
                fetchCharities();
            } catch (error) {
                enqueueSnackbar('Payout failed', { variant: 'error' });
            }
        }
    };

    const isNotFound = !loading && (currentTab === 'organizations' ? charityList.length === 0 : campaignList.length === 0);

    return (
        <Page title="Management: Charity & Campaigns">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading="Charity & Campaign Management"
                    links={[
                        { name: 'Dashboard', href: '/secure/app' },
                        { name: 'Charity' },
                    ]}
                    action={
                        <Stack direction={{ xs: 'column', sm: 'row' }} spacing={1}>
                            <Button
                                fullWidth={isMobile}
                                variant="contained"
                                color="info"
                                startIcon={<Iconify icon="eva:flash-fill" />}
                                onClick={handleProcessPayouts}
                            >
                                Process Payouts
                            </Button>
                            <Button
                                fullWidth={isMobile}
                                variant="contained"
                                startIcon={<Iconify icon="eva:plus-fill" />}
                                onClick={() => currentTab === 'organizations' ? handleOpenAddCharity() : setOpenAddCampaign(true)}
                            >
                                {currentTab === 'organizations' ? (isMobile ? 'Onboard' : 'Onboard Charity') : (isMobile ? 'Launch' : 'Launch Campaign')}
                            </Button>
                        </Stack>
                    }
                />

                <Tabs
                    value={currentTab}
                    scrollButtons="auto"
                    variant="scrollable"
                    allowScrollButtonsMobile
                    onChange={(e, value) => {
                        setCurrentTab(value);
                        setPage(0);
                    }}
                    sx={{ mb: 3 }}
                >
                    <Tab label={isMobile ? "Orgs" : "Organizations"} value="organizations" icon={<Iconify icon="eva:heart-fill" />} />
                    <Tab label={isMobile ? "Campaigns" : "Fundraising Campaigns"} value="campaigns" icon={<Iconify icon="eva:trending-up-fill" />} />
                </Tabs>

                <Card>
                    <UserListToolbar
                        numSelected={0}
                        filterName={filterName}
                        onFilterName={(val) => setFilterName(val)}
                        placeholder={isMobile ? "Search..." : "Search charities or campaigns..."}
                    />

                    <Scrollbar>
                        <TableContainer sx={{ minWidth: isMobile ? 400 : 800 }}>
                            <Table size={isMobile ? 'small' : 'medium'}>
                                <UserListHead
                                    headLabel={
                                        currentTab === 'organizations'
                                            ? CHARITY_HEAD.filter(h => isMobile ? ['name', 'available_balance', 'action'].includes(h.id) : true)
                                            : CAMPAIGN_HEAD.filter(h => isMobile ? ['title', 'progress', 'action'].includes(h.id) : true)
                                    }
                                />
                                <TableBody>
                                    {currentTab === 'organizations' ? (
                                        charityList.map((row) => (
                                            <TableRow hover key={row.id}>
                                                <TableCell padding="checkbox">
                                                    <Checkbox />
                                                </TableCell>
                                                <TableCell sx={{ minWidth: 120 }}>{row.name}</TableCell>
                                                {!isMobile && <TableCell>@{row.username}</TableCell>}
                                                {!isMobile && <TableCell><Label color="info">{row.category}</Label></TableCell>}
                                                {!isMobile && <TableCell>{row.cac_number || 'N/A'}</TableCell>}
                                                {!isMobile && <TableCell>{fCurrency(row.pending_balance)}</TableCell>}
                                                <TableCell sx={{ color: 'success.main', fontWeight: 'bold' }}>{fCurrency(row.available_balance)}</TableCell>
                                                {!isMobile && (
                                                    <TableCell>
                                                        <Label color={row.verification_status === 'verified' ? 'success' : 'warning'}>
                                                            {row.verification_status}
                                                        </Label>
                                                    </TableCell>
                                                )}
                                                <TableCell align="right">
                                                    <Box sx={{ display: 'flex', justifyContent: 'flex-end', gap: 0.5 }}>
                                                        <IconButton size="small" color="primary" onClick={() => handleEditCharity(row)}>
                                                            <Iconify icon="eva:edit-fill" />
                                                        </IconButton>
                                                        <IconButton size="small" color="error" onClick={() => handleDeleteCharity(row.id)}>
                                                            <Iconify icon="eva:trash-2-fill" />
                                                        </IconButton>
                                                    </Box>
                                                </TableCell>
                                            </TableRow>
                                        ))
                                    ) : (
                                        campaignList.map((row) => {
                                            const progress = (row.current_amount / row.target_amount) * 100;
                                            return (
                                                <TableRow hover key={row.id}>
                                                    <TableCell padding="checkbox">
                                                        <Checkbox />
                                                    </TableCell>
                                                    <TableCell sx={{ minWidth: 120 }}>{row.title}</TableCell>
                                                    {!isMobile && <TableCell>{row.charity_name}</TableCell>}
                                                    {!isMobile && <TableCell>{fCurrency(row.target_amount)}</TableCell>}
                                                    <TableCell sx={{ minWidth: isMobile ? 120 : 160 }}>
                                                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                                            <Box sx={{ width: '100%', mr: 1 }}>
                                                                <LinearProgress variant="determinate" value={progress > 100 ? 100 : progress} color={progress >= 100 ? 'success' : 'primary'} />
                                                            </Box>
                                                            <Typography variant="body2" color="text.secondary">{Math.round(progress)}%</Typography>
                                                        </Box>
                                                        <Typography variant="caption">{fCurrency(row.current_amount)} raised</Typography>
                                                    </TableCell>
                                                    {!isMobile && (
                                                        <TableCell>
                                                            <Label color={row.status === 'active' ? 'success' : row.status === 'emergency' ? 'error' : 'default'}>
                                                                {row.status}
                                                            </Label>
                                                        </TableCell>
                                                    )}
                                                    {!isMobile && (
                                                        <TableCell>
                                                            <Label variant="ghost" color={row.payout_status === 'released' ? 'success' : 'warning'}>
                                                                {row.payout_status}
                                                            </Label>
                                                        </TableCell>
                                                    )}
                                                    <TableCell align="right">
                                                        {row.payout_status !== 'released' && row.current_amount > 0 && (
                                                            <Tooltip title="Release Payout">
                                                                <IconButton size="small" color="success" onClick={() => handleSinglePayout(row.id)}>
                                                                    <Iconify icon="eva:checkmark-circle-2-fill" />
                                                                </IconButton>
                                                            </Tooltip>
                                                        )}
                                                        <IconButton size="small" color="error" onClick={() => handleDeleteCampaign(row.id)}>
                                                            <Iconify icon="eva:trash-2-fill" />
                                                        </IconButton>
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        })
                                    )}
                                </TableBody>

                                {isNotFound && (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={8} sx={{ py: 3 }}>
                                                <SearchNotFound searchQuery={filterName} />
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}
                            </Table>
                        </TableContainer>
                    </Scrollbar>

                    <TablePagination
                        rowsPerPageOptions={[10, 25, 50]}
                        component="div"
                        count={total}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={(e, p) => setPage(p)}
                        onRowsPerPageChange={(e) => setRowsPerPage(parseInt(e.target.value, 10))}
                    />
                </Card>
            </Container>

            {/* Onboard/Edit Charity Modal */}
            <Dialog open={openAddCharity} onClose={() => setOpenAddCharity(false)} maxWidth="sm" fullWidth fullScreen={isMobile}>
                <DialogTitle>{isEdit ? 'Edit Charity Organization' : 'Onboard New Charity Organization'}</DialogTitle>
                <DialogContent sx={{ pt: 2, display: 'flex', flexDirection: 'column', gap: 2 }}>
                    <TextField fullWidth label="Organization Name" value={charityForm.name} onChange={(e) => setCharityForm({ ...charityForm, name: e.target.value })} />

                    <Autocomplete
                        fullWidth
                        disabled={isEdit}
                        options={userOptions}
                        getOptionLabel={(option) => option.username}
                        loading={searchLoading}
                        onInputChange={(e, val) => handleUserSearch(val)}
                        onChange={(e, val) => setCharityForm({ ...charityForm, username: val?.username || '' })}
                        renderInput={(params) => (
                            <TextField {...params} label="System Username/Handle" placeholder="e.g. arewa_orphanage" />
                        )}
                        renderOption={(props, option) => (
                            <Box component="li" {...props} sx={{ display: 'flex', gap: 1 }}>
                                <Avatar src={option.profile_image} sx={{ width: 24, height: 24 }} />
                                {option.username} ({option.name})
                            </Box>
                        )}
                    />

                    <TextField fullWidth select label="Category" value={charityForm.category} onChange={(e) => setCharityForm({ ...charityForm, category: e.target.value })}>
                        {CATEGORIES.map((cat) => <MenuItem key={cat} value={cat}>{cat}</MenuItem>)}
                    </TextField>
                    <TextField fullWidth label="CAC Number (Optional)" value={charityForm.cac_number} onChange={(e) => setCharityForm({ ...charityForm, cac_number: e.target.value })} />

                    <TextField
                        fullWidth
                        multiline
                        rows={3}
                        label="Organization Description"
                        placeholder="Tell us about the mission..."
                        value={charityForm.description}
                        onChange={(e) => setCharityForm({ ...charityForm, description: e.target.value })}
                    />

                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                        <Button variant="outlined" component="label" startIcon={<Iconify icon="eva:image-fill" />}>
                            Upload Organization Logo
                            <input type="file" hidden accept="image/*" onChange={handleLogoUpload} />
                        </Button>
                        {charityForm.logo && (
                            <Avatar src={charityForm.logo} sx={{ width: 48, height: 48, border: '1px solid #eee' }} />
                        )}
                    </Box>

                    <TextField fullWidth select label="Verification Status" value={charityForm.verification_status} onChange={(e) => setCharityForm({ ...charityForm, verification_status: e.target.value })}>
                        <MenuItem value="verified">Verified</MenuItem>
                        <MenuItem value="unverified">Unverified</MenuItem>
                    </TextField>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setOpenAddCharity(false)}>Cancel</Button>
                    <Button variant="contained" onClick={handleAddCharity}>{isEdit ? 'Update Charity' : 'Onboard Charity'}</Button>
                </DialogActions>
            </Dialog>

            {/* Launch Campaign Modal */}
            <Dialog open={openAddCampaign} onClose={() => setOpenAddCampaign(false)} maxWidth="sm" fullWidth fullScreen={isMobile}>
                <DialogTitle>Launch New Fundraising Campaign</DialogTitle>
                <DialogContent sx={{ pt: 2, display: 'flex', flexDirection: 'column', gap: 2 }}>
                    <TextField fullWidth select label="Select Charity" value={campaignForm.charity_id} onChange={(e) => setCampaignForm({ ...campaignForm, charity_id: e.target.value })}>
                        {charityList.map((c) => <MenuItem key={c.id} value={c.id}>{c.name} (@{c.username})</MenuItem>)}
                    </TextField>
                    <TextField fullWidth label="Campaign Title" value={campaignForm.title} onChange={(e) => setCampaignForm({ ...campaignForm, title: e.target.value })} />
                    <TextField fullWidth multiline rows={3} label="Description" value={campaignForm.description} onChange={(e) => setCampaignForm({ ...campaignForm, description: e.target.value })} />

                    <Box sx={{ display: 'flex', gap: 2 }}>
                        <TextField fullWidth select label="State" value={campaignForm.state} onChange={(e) => setCampaignForm({ ...campaignForm, state: e.target.value, lga: NIGERIAN_LOCATIONS[e.target.value][0] })}>
                            {STATES.map((s) => <MenuItem key={s} value={s}>{s}</MenuItem>)}
                        </TextField>
                        <TextField fullWidth select label="LGA" value={campaignForm.lga} onChange={(e) => setCampaignForm({ ...campaignForm, lga: e.target.value })}>
                            {(NIGERIAN_LOCATIONS[campaignForm.state] || []).map((l) => <MenuItem key={l} value={l}>{l}</MenuItem>)}
                        </TextField>
                    </Box>

                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                        <Button variant="outlined" component="label" startIcon={<Iconify icon="eva:image-fill" />}>
                            Campaign Picture
                            <input type="file" hidden accept="image/*" onChange={handleCampaignImage} />
                        </Button>
                        {campaignForm.image && (
                            <Avatar variant="rounded" src={campaignForm.image} sx={{ width: 100, height: 60, border: '1px solid #eee' }} />
                        )}
                    </Box>

                    <TextField fullWidth label="Target Amount ()" type="number" value={campaignForm.target_amount} onChange={(e) => setCampaignForm({ ...campaignForm, target_amount: e.target.value })} />
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setOpenAddCampaign(false)}>Cancel</Button>
                    <Button variant="contained" color="success" onClick={handleAddCampaign}>Launch Campaign</Button>
                </DialogActions>
            </Dialog>
        </Page>
    );
}
